import { useState } from 'react';
import EcoSystemSvg from './components/EcoSystemSvg/EcoSystemSvg';
import Swiper from './components/Swiper/Swiper';
import Style from './EcoSystem.module.scss';

function EcoSystem() {
  const [firstIndex, setFirstIndex] = useState(0);

  const [secondIndex, setSecondIndex] = useState(0);

  const list = [];

  const list2 = [];

  return (
    <div className={Style.EcoSystem}>
      <div className={Style.EcoSystemLeft}>
        <Swiper list={list} index={firstIndex} onIndexChange={setFirstIndex} />
      </div>
      <div className={Style.EcoSystemCenter}>
        <EcoSystemSvg
          firstIndex={firstIndex}
          secondIndex={secondIndex}
          onFirstIndexChange={setFirstIndex}
          onSecondIndexChange={setSecondIndex}
        />
      </div>
      <div className={Style.EcoSystemRight}>
        <Swiper list={list2} index={secondIndex} onIndexChange={setSecondIndex} />
      </div>
    </div>
  );
}

export default EcoSystem;
