/* eslint-disable react/no-array-index-key */
import { useState } from 'react';
/* eslint-disable import/no-unresolved */
import { Swiper, SwiperSlide } from 'swiper/react';

// eslint-disable-next-line import/no-unresolved
import 'swiper/swiper.min.css';

function EcoSwiper({ onIndexChange, list }) {
  const [swiperRef, setSwiperRef] = useState();

  return (
    <div>
      <Swiper
        className="Swiper"
        direction="vertical"
        slidesPerView={3}
        spaceBetween={30}
        centeredSlides
        watchSlidesProgress
        onSlideChange={(index) => {
          onIndexChange?.();
          console.log('slide change', index);
        }}
        onSwiper={(swiper) => setSwiperRef(swiper)}
        onSetTransition={(transition) => {
          for (let i = 0; i < swiperRef?.slides.length; i++) {
            const slide = swiperRef?.slides?.eq(i);
            slide?.transition(transition);
          }
        }}
        onSetTranslate={({ slides }) => {
          for (let i = 0; i < slides.length; i++) {
            const slide = slides.eq(i);
            const { progress } = slides[i];
            slide.transform(`scale(${1 - Math.abs(progress) / slides.length})`);
          }
        }}>
        {list.map((item, i) => (
          <SwiperSlide key={i}>{item}</SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
}

export default EcoSwiper;
