import { useEffect } from 'react';
import { createUseGesture, dragAction, wheelAction } from '@use-gesture/react';

let startY;

export function useGesture({ onWheelEnd, onDrag, onDragEnd }) {
  const setStartY = (v) => {
    startY = v;
  };

  const useLocalGesture = createUseGesture([dragAction, wheelAction]);

  const bind = useLocalGesture(
    {
      onWheelStart({ movement: [, my] }) {
        setStartY(my);
      },
      // onWheel({ down, movement: [, my] }) {},
      onWheelEnd({ movement: [, my] }) {
        onWheelEnd?.(my > 300);
      },
      onDrag({ movement: [, my] }) {
        onDrag?.(my);
        setStartY(my);
      },
      onDragStart: ({ movement: [, my] }) => {
        if (!startY) {
          setStartY(my);
        }
      },
      onDragEnd: ({ movement: [, my] }) => {
        setStartY(0);

        if (Math.abs(my - startY) >= 100) {
          onDragEnd?.(my < startY);
        }
      },
    },
    { drag: {} },
  );

  useEffect(() => () => setStartY(0), []);

  return {
    bind,
  };
}
