/* eslint-disable react/jsx-props-no-spreading */
import React from 'react';
import { useHistory } from 'react-router-dom';
import { useGesture } from '../../hooks/useGesture';
import { useDisableTouchMove } from '../../hooks/useDisableTouchMove';
import Style from './Home.module.scss';

const Home = () => {
  useDisableTouchMove();

  const history = useHistory();

  const goSolution = () => history.push('/solution');

  const { bind } = useGesture({
    onDragEnd(status) {
      if (status) {
        goSolution();
      }
    },
    onWheelEnd(status) {
      console.log('111');
      if (status) {
        goSolution();
      }
    },
  });

  return (
    <div className={Style.Home} {...bind()}>
      <div className={Style.HomeLogo}>
        <img src="/static/images/home/ekeaTekText.svg" alt="ekeaTek" />
        <div className={Style.HomeLogoText}>INTELLIGENT TRANSPARENT GIVING</div>
      </div>
      <div className={Style.Background}>
        <video
          src="/static/images/home/EleatekHome.mp4"
          autoPlay
          loop
          muted
          playsInline
          className={Style.BackgroundVideo}
        />
      </div>

      <div className={Style.Arrow} onClick={goSolution} />
    </div>
  );
};

export default Home;
