import { Switch, Route, Redirect } from 'react-router-dom';
import Layout from '../components/Layout';
import Home from '../views/Home/Home';
import Solution from '../views/Solution/Solution';
import EcoSystem from '../views/EcoSystem/EcoSystem';
import VolunteerAndAdvocates from '../views/VolunteerAndAdvocates/VolunteerAndAdvocates';
import Partners from '../views/Partners/Partners';
import Roadmap from '../views/Roadmap/Roadmap';


const AppRoutes = () => (
  <Layout>
    <Switch>
      <Route path="/" exact>
        <Home />
      </Route>
      <Route path="/solution" exact>
        <Solution />
      </Route>
      <Route path="/ecoSystem" exact>
        <EcoSystem />
      </Route>
      <Route path="/volunteerAndAdvocates" exact>
        <VolunteerAndAdvocates />
      </Route>
      <Route path="/partners" exact>
        <Partners />
      </Route>
      <Route path="/roadmap" exact>
        <Roadmap />
      </Route>
      

      <Redirect to="/" />
    </Switch>
  </Layout>
);

export { AppRoutes };
