import { useEffect } from 'react';

export function useDisableTouchMove() {
  useEffect(() => {
    function disableTouch(e) {
      e.preventDefault();
    }

    document.body.addEventListener('touchmove', disableTouch, { passive: false });
    return () => window.removeEventListener('resize', disableTouch);
  }, []);

  return {};
}
